@font-face {
  font-family: 'AvenirNextLTPro-Regular';
  src: local('AvenirNextLTPro-Regular'), url('./fonts/AvenirNextLTPro-Regular.woff'), /* Modern Browsers */
       local('AvenirNextLTPro-Regular'),  url('./fonts/AvenirNextLTPro-Regular.ttf')  format('truetype'); /* Safari, Android, iOS */    
  font-style:   normal;
}

* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}
html {
    height:100%;
    font-size: 14px;
    font-weight: 400;
}
body {
    height:100%;
    margin:0px;
    font-size: 14px;
    font-family: AvenirNextLTPro-Regular,OpenSans-Light,Lato,sans-serif;
}
#root{
    height:100%;
}
h3 {
    margin-top: 0;
}
input[type="radio"] {
  margin: 3px;
}
input[type="radio"][disabled] + label {
  color: #999;
}
select {
  border: 0px;
  width: 100%;
  height: 110px;
  outline: 0;
  overflow-y: auto;
}
option:checked {
  background: linear-gradient(0deg, #009ee3 0%, #009ee3 100%);
}
.delete-panel {
  width: 100%;
}
.advanced-link {
  color: #009ee3;
  font-weight: 700;
  cursor: pointer;
  margin-bottom:8px;
}
.delete-info {
  background-color: #f5f6f8;
  padding: 10px;
  margin: 10px 0px;
  border: 2px solid #009ee3;
  border-top: none;
  border-right: none;
  border-bottom: none;
  color: #666;
}
.delete-info.warning {
  border: 2px solid orange;
  border-top: none;
  border-right: none;
  border-bottom: none;
  color: #333;
}
.delete-info.error {
  border: 2px solid red;
  border-top: none;
  border-right: none;
  border-bottom: none;
  color: #333;
}

.state-block {
    padding:16px 16px 8px 16px;
}

.delete-buttons {
  text-align: right;
  position:fixed;
  right:16px;
  bottom:16px;
}
.delete-buttons.trashcan-btn {
  float: left;
}
button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button, select {
    text-transform: none;
}
button, html input[type=button], input[type=reset], input[type=submit] {
    -webkit-appearance: button;
    cursor: pointer;
}
.btn-default, button {
    background: none;
}
button {
    overflow: visible;
}
.basic-btn {
    padding: 5px 20px;
    display: inline-block;
    cursor: pointer;
    font-size:14px;
    line-height: 14px;
    height: 25px;
    border-radius: 4px;
    font-weight: 600;
    -webkit-font-smoothing: antialiased;
    /* background-color: #0095e0;*/
    border: 1px solid #0095e0; 
    color: #fff;
}

.basic-btn.pale {
    background-color: #fff;
    color: #777;
    border-color: #e2e2e2;
}
.basic-btn.ml10 {
    margin-left: 10px;
}
.basic-btn.disabled, .basic-btn:disabled {
    cursor: default;
}
.basic-btn.disabled, .basic-btn.disabled:hover, .basic-btn:disabled, .basic-btn:disabled:hover {
    background-color: #eee;
    border-color: #eee;
    color: #fff;
}
.powered-by {
  position:fixed;
  bottom:21px;
  left:16px;
}
.powered-by a{
  text-decoration: none;
  color:#e2e2e2;
  font-size:10px;
  margin:0px;
  padding:0px;
}
.powered-by a:hover{
  text-decoration: none;
  color:#444;
}
.hide-me {
  display:none;
}

.pointer{
  cursor:pointer;
}

input[type="checkbox"] { margin-right:8px; cursor:pointer;}

.center {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}